/*Desktop*/
.container {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    padding: 0px 10%;
    margin-bottom: 40px;

    & img {
        height: 50px;
    }

    & .navbar {
        display: flex;
        gap: 40px;
        align-items: center; 

        & .link-nav{
            color: #FF6060;
            font-weight: 500;
            font-size: 24px;
            font-family: "Montserrat", sans-serif;
            line-height: 142.6%; 
        }
    }
}

.link-nav:focus, 
.link-nav:active{
    text-decoration: underline solid !important; 
    color: #FF6060 !important; 
}

/*Breakpoint*/
/* Tablette et écran plus grand */
@media (min-width: 992px) and (max-width: 1199px){
    .container{
        padding:20px 70px 0px 70px;
        margin-bottom: 30px;
    }
}

/* Mobile */
@media (min-width: 768px) and (max-width: 991px){
    .container{
        padding: 0px 65px 0px 50px;
        margin-bottom: 30px;
    }
}

/*Mobile taille inférieure*/
@media (min-width: 375px) and (max-width: 767px){
    .container{
        margin: 0px 15px 20px 15px;
        padding: 0px;

        & img {
            height: 30px;
        }

        & .navbar{
        
            & .link-nav{
                font-size: 12px;  
            }
        }
    }
} 

@media (min-width: 280px) and (max-width: 374px){
    .container{
      margin: 0px 15px 20px 15px;
      padding: 0px;

        & img {
            height: 30px;
        }

        & .navbar{

            & .link-nav{
                font-size: 12px;  
            }
        } 
    }
}